.resume_container{
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.875rem;
}

.timeline{
    background-color:rgb(246, 210, 210);
    padding: 1.875rem;
    border-radius: 15px;
    position: relative;
    box-shadow: var(--shadow);
}
.timeline_item{
    position: relative;
    padding-left: 3.125rem;
    padding-bottom: 3.125rem;
}

.timeline_item:last-child{
    padding-bottom: 0;

}
.timeline_item::before{
    content: '';
    width: 1px;
    height: 100%;
    background-color: var(--first-color);
    position: absolute;
    left: 0.25rem;
    top: 0;
}

.timeline .la-graduation-cap,
.timeline .la-briefcase{
    position: absolute;
    left: -0.4375rem;
    top: 0;
    font-size: var(--h2-font-size);
    color: var(--first-color);
    background-color: var(--container-color);
    padding: 0.4375rem 0;
}
.timeline_date{
    color: #645beb;
    font-size: var(--small-font-size);
}
.timeline_title{
    font-size: var(--h2-font-size);
    margin: 0.5 rem 0;
}
.timeline_marks{
    font-size: var(--small-font-size); 
    color: rgb(74, 176, 142);
    position: absolute;
    left: 11rem;
    top: 3.5;
    
}

@media screen  and (max-width:1024px){
.resume_container{
    grid-template-columns: 450px;
    justify-content: center;
    row-gap: 1.875rem;
}
 .timeline_item::before{
    left: 2px;
 }
}

@media screen  and (max-width:576px){
.resume_container{
    grid-template-columns: 1fr;
}


}
