@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,700;1,500&family=Rubik:wght@300;700&display=swap');

*{
margin: 0;
padding: 0;
box-sizing: border-box;


}

html{
    scroll-behavior: smooth;
}




:root{
    /*    variables in css    */

    --first-color: rgb(220, 117, 134);
    --title-color:rgb(54, 54, 107);
    --text-color: rgb(75, 120, 137);
    --body-color: white;
    --container-color:#fff;



    /*     font and type  */
    --body-font:'Rubik', sans-serif;
    --h1-font-size:2.25rem;
    --h2-font-size:1.5rem;
    --h3-font-size:1.25rem;
    --normal-font-size:1rem;
    --small-font-size:0.875rem;
    --smaller-font-size:0.813rem;

    /*  font weight     */

    --font-medium: 500;
    --font-semibold:600;
    --font-bold:700;


    /* box shadow */
    --shadow: 0px 5px 20px 0px rgb(69 67 96 / 10%)

    /*  border radius   */

    --border-radius:20px;
    


}

@media screen and (max-width:1024px){
    :root{
        --h1-font-size:1.75rem;
    --h2-font-size:1.25rem;
    --h3-font-size:1rem;
    --normal-font-size:0.938rem;
    --small-font-size:0.813rem;
    --smaller-font-size:0.75rem;

    }
}

body,button,input,textarea{
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    
    }

    body{

        background-color: var(--body-color);
        line-height: 1.7;
        color: var(--text-color);
    }

    h1,h2,h3{
        color: var(--title-color);
        font-weight: var(--font-semibold);
        line-height: 1.2;
    }

    ul{
       list-style: none; 
    }
    a{
        text-decoration: none;
    }

    button{
        cursor: pointer;
        border: none;
        outline: none;
    }

    img{

        max-width: 100%;
        height: auto;
        vertical-align: middle;
    }

    .container{
        max-width:1080px ;
        padding-left: 15px;
        padding-right: 15px;
        margin: 0 auto;
    }

    .grid{
        display: grid;
    }

    .section{
        padding-top: 7rem;
    }
    .section_title{
        font-size: var(--h1-font-size);
        margin-left:0.875rem ;
        font-weight: var(--font-bold);
        position: relative;
        margin-bottom: 3.75rem;
    }
    .section_title::before{
        content:'' ;
        background: url('logo.svg');
        height: 2.25rem;
        width: 2.25rem;
        position: absolute;
        left:-0.875 ;
        top: -0.875rem;
    }
    .btn{
        padding: 0.75rem 2rem;
        line-height: 1;
        border-radius: 1.875rem;
        box-shadow: 0 0 1px rgb(0 0 0 / 0%);
        border: 1px solid transparent;
        color: #fff;
        display: inline-block;
        background-color: var(--first-color);
        font-weight: var(--font-bold);

    }
    @keyframes button-push{
        50%{
            transform: scale(0.8);
        }
        100%{
            transform: scale(1);
        }
    }
    .btn:hover{
        animation:button-push 0.3s linear 1 ;

    }